*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
}

body {
    font-family: $font_family;
    background-color: $color_background-gray;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;

    font-weight: normal;
}

p {
    margin: 0;
}

ul {
    margin: 0;
    padding: 0;

    list-style: none;
}

img,
svg {
    display: block;
}

a {
    text-decoration: none;
    color: black;
}

table {
    border-collapse: collapse;
    border-spacing: 0;

    td {
        padding: 0;
    }
}

button {
    border: none;
    background-color: transparent;

    cursor: pointer;
}